import React from "react";
import {ChangeEvent, useMemo, useState} from "react";

export const useDynamicInputs = (inputList: {initialValue: number, name: string, label: string}[]) => {
  const initialInputsState = useMemo(() => {
    const initialState: {[k: string]: number} = {};
    inputList.forEach((input) => {
      initialState[input.name] = input.initialValue;
    });
    return initialState;
  }, [inputList]);

  const [values, setValues] = useState(initialInputsState);

  const Inputs = () => {
    const handleInputChange = (e: ChangeEvent<HTMLInputElement>) => {
      const { name, value } = e.target;
      const newValue = parseFloat(value);
      if (isNaN(newValue)) return;

      setValues((prevValues) => ({
        ...prevValues,
        [name]: newValue,
      }));
    };

    return (
      <div>
        {inputList.map((input) => (
            <div key={input.name}>
            <label htmlFor={input.name}>{input.label}</label>
              <input
          type="number"
          id={input.name}
          name={input.name}
          value={values[input.name]}
          onChange={handleInputChange}
    />
    </div>
  ))}
    </div>
  );
  };

  return { values, Inputs };
};