import { useState } from "react"
import { Application, IApplicationOptions } from "pixi.js"
import { Stage } from "@pixi/react"
import React from "react";
import { useDynamicInputs } from "./useDynamicInputs"

export interface StaticSketchProps {
  width?: number;
  height?: number;
  applicationOptions?: Partial<IApplicationOptions>;
  sketchOptions?: {initialValue: number, name: string, label: string}[];
  renderContent: (app: Application, options: {[k: string]: number}) => void;
}

export const StaticSketch = ({ width, height, applicationOptions, sketchOptions, renderContent }: StaticSketchProps) => {
  const [app, setApp] = useState<Application>();

  const { values: options, Inputs: OptionsInputs } = useDynamicInputs(sketchOptions || []);

  const render = () => {
    if (!app) return;

    app.stage.removeChildren();

    renderContent(app, options);

    app?.renderer.render(app?.stage)
  }

  return (
    <>
      <Stage
        width={width ? width : 512}
        height={height ? height : 512}
        raf={false}
        renderOnComponentChange={false}
        onMount={setApp}
        options={applicationOptions}
      />

      <OptionsInputs />

      <div>
        <button
          style={{ margin: '20px auto 0 0' }}
          onClick={render}
        >
          Render
        </button>
      </div>
    </>
  );
}